<script>
import config from "@/common/config";
import customTranslate from "@/common/customTranslate";
import service from "@/common/service";
import moment from "moment";
import BpmnJS from "bpmn-js/lib/Modeler";
export default {
  // el: "#app",
  data() {
    return {
      user: null,
      processes: [],
      process: {},
      bpmn: null,
      moment: moment,
      config: config,
    };
  },
  created: function () {
    var translate = {
      translate: ["value", customTranslate],
    };
    this.bpmn = new BpmnJS({
      additionalModules: [translate],
    });
    this.refresh();
  },
  methods: {
    refresh: function () {
      this.process = {};
      service.getItems("process").then((processes) => {
        this.processes = processes;
      });
    },
    selectProcess(process) {
      this.process = process;
      return this.bpmn.importXML(this.process.xml).then((response) => {
        $("#processPreview").html(this.process.svg);
      });
    },
    editProcess() {
      if (!this.process || !this.process._id) {
        return;
      }
      this.$router.push({
        name: "processEdit",
        query: { id: this.process._id },
      });
    },
    copyProcess() {
      if (!this.process || !this.process._id) {
        return;
      }
      this.$router.push({
        name: "processEdit",
        query: { sourceId: this.process._id },
      });
    },
    deleteProcess() {
      if (!this.process._id) {
        return;
      }
      service.deleteItem("process", this.process._id).then((result) => {
        this.refresh();
        $("#deleteModal").modal("hide");
      });
    },
  },
};
</script>

